 
  /* Hide the file input using
  opacity */
  [type=file] {
      position: absolute;
      filter: alpha(opacity=0);
      opacity: 0;
  }
  
  [type=file] + label {
    border: 1px solid #CCC;
    border-radius: 3px;
    text-align: left;
    padding: 10px;
    width: 150px;
    margin: 0;
    left: 0;
    position: relative;
  }
  [type=file] + label {
    text-align: center;
    left: 7.35em;
    top: 0.5em;
    /* Decorative */
    background: #333;
    color: #fff;
    border: none;
    cursor: pointer;
  }
  [type=file] + label:hover {
    background: #3399ff;
  }

  .upload-photo {
    width: 150px;
    height: 150px;
    justify-content: center;
    align-items: center;
    display: flex;
    border: 0.5px solid black;
    background: #6095C9;
  }

  .upload-button {
    position: absolute;
    top: 50px;
    left: 9px;
  }

  .badge-default::after {
    content: ' *';
    color: red;
  }